/* Global styles */
body {
  font-family: 'Courier New', monospace;
  margin: 0;
  padding: 0;
  background-color: #FAF3DD;  /* Changed to a lighter color for better contrast */
  color: #5E6472;
}

.response {
  margin-top: 40px;
  padding: 20px;
  background: #AED9E0;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.response h2 {
  color: #FFA69E;
  margin-top: 0;
}

.response p {
  white-space: pre-wrap; /* Preserve line breaks and spaces */
}
